import React, { Component } from 'react';
import {getToken} from '@util/token';

import Icon from '@component/common/icon';
import Avator from '@component/common/avator';
import {showDialog, closeDialog} from '@component/common/dialog';
import './index.css';
export default class SlideBar extends Component {

    state = {
        showPanel: false
    }
    showAddRosterPanel = () => {
        // this.setState({
        //     showPanel: true
        // });

        showDialog({
            content:
                <div className="input-container">
                    <input type="text" ref={(ins) => {this.nickname=ins;}}className="input" placeholder="" />
                </div>,
           
            footer:<div className="footer">
                    <button className="button" onClick = {this.addRoster}></button>
                </div>,
            title: ''
        })
    }
    addRoster = () => {
        if (this.nickname) {
            let value = this.nickname.value;
            if (!value.trim()) {
                alert('');
                return;
            }
        }
        sdk.conn.subscribe({
            to: this.nickname.value,
            message: '!'   
        });

       closeDialog();

    }
    render() {
        let tokenUser = getToken();
        let {showPanel} = this.state;
        let username = tokenUser ? tokenUser.user.username : '';
        return (
            <div className="slidebar">
                <div className="profile">
                    <div className="app-item">
                        <Avator />
                    </div>
                    {/*<span className="iconfont icon-usered" />*/}
                    <div className="name">{username}</div>
                </div>
                <div className="menus">
                    <div className="app-item chat">
                        <Icon type="chat" />
                    </div>
                    <div className="app-item group">
                        <Icon type="chat1" />
                    </div>
                </div>
                <div className="footer">
                    <div className="app-item setting" onClick = {this.showAddRosterPanel}>
                        <Icon type="setting1" />
                    </div>
                </div>

                
            </div>
        );
    }
}





